
import { defineComponent, ref } from 'vue';
import { Radio } from '@/components';
import Container from './component/container.vue';
import Attributes from './component/attributes.vue';

export default defineComponent({
  name: 'radioExample',
  components: {
    Container,
    Attributes,
    KiRadio: Radio,
    KiRadioGroup: Radio.Group,
  },
  setup() {
    const radio = ref('1');
    const radio1 = ref('2');
    const radio3 = ref('3');
    const radio4 = ref('1');
    const events = [
      {
        name: 'change',
        description: '',
        type: '(value: string | number | boolean) => void',
      },
    ];
    const attribute = [
      {
        attribute: 'v-model',
        description: '',
        type: 'string | number | boolean',
        option: '',
        default: '',
      },
      {
        attribute: 'label',
        description: 'Radio  value',
        type: 'string | number | boolean',
        option: '',
        default: '',
      },
      {
        attribute: 'disabled',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
      {
        attribute: 'border',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
    ];
    return {
      radio,
      radio1,
      radio3,
      radio4,
      events,
      attribute,
    };
  },
});
