
import { defineComponent, reactive, ref } from 'vue';
import {
  Form, Input, Button, Message, Select,
} from '@/components';
import Container from './component/container.vue';
import Attribute from './component/attributes.vue';

export default defineComponent({
  name: 'FormExample',
  components: {
    KiForm: Form,
    KiFormItem: Form.Item,
    KiInput: Input,
    KiButton: Button,
    KiSelect: Select,
    KiOption: Select.Option,
    Container,
    Attribute,
  },
  setup() {
    const username1 = reactive({
      name: '',
      password: '',
    });

    const username2 = reactive({
      name: 'zhangsan',
      password: '',
      repeatPassword: '',
      hobby: '',
    });

    const username3 = reactive({
      name: '',
      password: '',
    });

    const formRef = ref();

    const handleCreate = () => {
      if (formRef.value) {
        formRef.value.$.validate().then((status: boolean) => {
          if (status) {
            Message.success('');
          } else {
            Message.error('');
          }
        });
      }
    };

    const handleReset = () => {
      if (formRef.value) {
        formRef.value.$.reset();
      }
    };

    const validatorFunction = (rule: any, value: any, callback: any) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== username2.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };

    const rules = {
      name: [
        { required: true, message: '', trigger: 'change' },
        { min: 3, message: ' 3 ', trigger: 'change' },
      ],
      password: [
        { required: true, message: '', trigger: 'change' },
        { min: 6, message: ' 6 ', trigger: 'change' },
        { max: 12, message: ' 12 ', trigger: 'change' },
      ],
      repeatPassword: [
        { validator: validatorFunction, trigger: 'change', required: true },
      ],
      hobby: [
        { required: true, message: '', trigger: 'change' },
      ],
    };

    const attributes = [
      {
        attribute: 'model',
        description: '',
        type: 'object',
        option: '',
        default: '',
      },
      {
        attribute: 'rules',
        description: '',
        type: 'object',
        option: '',
        default: '',
      },
      {
        attribute: 'showMessage',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: 'true',
      },
      {
        attribute: 'labelWidth',
        description: 'label ',
        type: 'string',
        option: '',
        default: 'auto',
      },
    ];

    const events = [
      {
        name: 'validate',
        description: ' Promise .then ',
        type: '(callback: (val: boolean) => void) => Promise<boolean>',
      },
      {
        name: 'reset',
        description: '',
        type: '() => void',
      },
    ];

    const itemAttributes = [
      {
        attribute: 'label',
        description: '',
        type: 'string',
        option: '',
        default: '',
      },
      {
        attribute: 'prop',
        description: ' model  validatereset ',
        type: 'string',
        option: ' Form  model ',
        default: '',
      },
      {
        attribute: 'showMessage',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: '',
      },
      {
        attribute: 'required',
        description: ' prop ',
        type: 'boolean',
        option: 'true | false',
        default: '',
      },
      {
        attribute: 'requiredErrorMsg',
        description: ' required  msg',
        type: 'string',
        option: '',
        default: '{prop} field is required',
      },
      {
        attribute: 'requiredTrigger',
        description: ' required ',
        type: 'string',
        option: 'change | blur',
        default: 'blur',
      },
    ];

    return {
      username1,
      username2,
      username3,
      rules,
      formRef,
      attributes,
      events,
      itemAttributes,
      handleCreate,
      handleReset,
    };
  },
});
