
import { defineComponent } from 'vue';
import { Tag } from '@/components';
import Container from './component/container.vue';
import Attributes from './component/attributes.vue';

export default defineComponent({
  name: 'TagExample',
  components: {
    KiTag: Tag,
    Container,
    Attributes,
  },
  setup() {
    const events = [
      {
        name: 'on-close',
        description: ' Tag ',
        type: '() => void',
      },
    ];
    const attributes = [
      {
        attribute: 'type',
        description: '',
        type: 'string',
        option: 'primary | small | error | warning | info',
        default: 'primary',
      },
      {
        attribute: 'size',
        description: '',
        type: 'string',
        option: 'mini | success | medium | large',
        default: 'medium',
      },
      {
        attribute: 'closable',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
      {
        attribute: 'color',
        description: ' type ',
        type: 'string',
        option: '',
        default: '',
      },
      {
        attribute: 'font-color',
        description: ' color ',
        type: 'string',
        option: '',
        default: '',
      },
    ];
    return {
      attributes,
      events,
    };
  },
});
