<template>
  <div>
    <Container
      title=''
      sub-title='Radio '
    >
      <template #default>
        <ki-radio v-model='radio' label='1'>1</ki-radio>
        <ki-radio v-model='radio' label='2'>2</ki-radio>
        <ki-radio v-model='radio' label='3'>3</ki-radio>
      </template>
    </Container>
    <Container
      title=''
      sub-title='Radio '
    >
      <template #default>
        <ki-radio v-model='radio1' label='1' disabled>1</ki-radio>
        <ki-radio v-model='radio1' label='2' disabled>2</ki-radio>
        <ki-radio v-model='radio1' label='3' disabled>3</ki-radio>
      </template>
    </Container>
    <Container
      title=''
      sub-title=''
    >
      <template #default>
        <ki-radio-group v-model='radio3'>
          <ki-radio label='1'>1</ki-radio>
          <ki-radio label='2'>2</ki-radio>
          <ki-radio label='3'>3</ki-radio>
        </ki-radio-group>
      </template>
    </Container>
    <Container
      title=' Radio'
      sub-title=''
    >
      <template #default>
        <ki-radio v-model='radio4' label='1' border>1</ki-radio>
        <ki-radio v-model='radio4' label='2' border>2</ki-radio>
        <ki-radio v-model='radio4' label='3' border>3</ki-radio>
      </template>
    </Container>
    <Attributes :attributes='attribute'/>
    <Attributes :attributes='events' target='event'/>
    <Attributes :attributes='events' target='event' title='RadioGroup Attributes Event'/>
  </div>
</template>

<script lang='ts'>
import { defineComponent, ref } from 'vue';
import { Radio } from '@/components';
import Container from './component/container.vue';
import Attributes from './component/attributes.vue';

export default defineComponent({
  name: 'radioExample',
  components: {
    Container,
    Attributes,
    KiRadio: Radio,
    KiRadioGroup: Radio.Group,
  },
  setup() {
    const radio = ref('1');
    const radio1 = ref('2');
    const radio3 = ref('3');
    const radio4 = ref('1');
    const events = [
      {
        name: 'change',
        description: '',
        type: '(value: string | number | boolean) => void',
      },
    ];
    const attribute = [
      {
        attribute: 'v-model',
        description: '',
        type: 'string | number | boolean',
        option: '',
        default: '',
      },
      {
        attribute: 'label',
        description: 'Radio  value',
        type: 'string | number | boolean',
        option: '',
        default: '',
      },
      {
        attribute: 'disabled',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
      {
        attribute: 'border',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
    ];
    return {
      radio,
      radio1,
      radio3,
      radio4,
      events,
      attribute,
    };
  },
});
</script>

