<template>
  <div>
    <Container
      title=''
    >
      <ki-form :model='username1' style='width: 400px;' label-width='70px'>
        <ki-form-item label=''>
          <ki-input v-model='username1.name' />
        </ki-form-item>
        <ki-form-item label=''>
          <ki-input v-model='username1.password' type='password' />
        </ki-form-item>
        <ki-form-item>
          <ki-button type='primary'></ki-button>
        </ki-form-item>
      </ki-form>
      <p class='tip'>
         form  form  @submit.native.prevent
      </p>
    </Container>
    <Container
      title=''
      sub-title=''
    >
      <ki-form :model='username2' style='width: 400px;' label-width='90px' :rules='rules' ref='formRef'>
        <ki-form-item label='' prop='name'>
          <ki-input v-model='username2.name' />
        </ki-form-item>
        <ki-form-item label='' prop='password'>
          <ki-input v-model='username2.password' type='password' show-password />
        </ki-form-item>
        <ki-form-item label='' prop='repeatPassword'>
          <ki-input v-model='username2.repeatPassword' type='password' show-password />
        </ki-form-item>
        <ki-form-item label='' prop='hobby'>
          <ki-select v-model='username2.hobby'>
            <ki-option label='' value='0'></ki-option>
            <ki-option label='' value='1'></ki-option>
            <ki-option label='' value='2'></ki-option>
          </ki-select>
        </ki-form-item>
        <ki-form-item>
          <ki-button type='primary' style='margin-right: 10px;' @click='handleCreate'></ki-button>
          <ki-button @click='handleReset'></ki-button>
        </ki-form-item>
      </ki-form>
      <p class='tip'>
          <a href='https://github.com/yiminghe/async-validator' target='_blank'>async-validator</a>
      </p>
    </Container>
    <Container
      title=''
      sub-title=' form-item  required=true  prop  rules requiredErrorMsg requiredTrigger  blur'
    >
      <ki-form :model='username3' style='width: 400px;' label-width='70px'>
        <ki-form-item label='' required requiredErrorMsg='' prop='name'>
          <ki-input v-model='username3.name' />
        </ki-form-item>
        <ki-form-item label='' required prop='password' requiredTrigger='change'>
          <ki-input v-model='username3.password' type='password' />
        </ki-form-item>
      </ki-form>
    </Container>
    <Attribute :attributes='attributes' />
    <Attribute :attributes='events' target='event' title='Form Events' />
    <Attribute :attributes='itemAttributes' />
  </div>
</template>

<script lang='ts'>
import { defineComponent, reactive, ref } from 'vue';
import {
  Form, Input, Button, Message, Select,
} from '@/components';
import Container from './component/container.vue';
import Attribute from './component/attributes.vue';

export default defineComponent({
  name: 'FormExample',
  components: {
    KiForm: Form,
    KiFormItem: Form.Item,
    KiInput: Input,
    KiButton: Button,
    KiSelect: Select,
    KiOption: Select.Option,
    Container,
    Attribute,
  },
  setup() {
    const username1 = reactive({
      name: '',
      password: '',
    });

    const username2 = reactive({
      name: 'zhangsan',
      password: '',
      repeatPassword: '',
      hobby: '',
    });

    const username3 = reactive({
      name: '',
      password: '',
    });

    const formRef = ref();

    const handleCreate = () => {
      if (formRef.value) {
        formRef.value.$.validate().then((status: boolean) => {
          if (status) {
            Message.success('');
          } else {
            Message.error('');
          }
        });
      }
    };

    const handleReset = () => {
      if (formRef.value) {
        formRef.value.$.reset();
      }
    };

    const validatorFunction = (rule: any, value: any, callback: any) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== username2.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };

    const rules = {
      name: [
        { required: true, message: '', trigger: 'change' },
        { min: 3, message: ' 3 ', trigger: 'change' },
      ],
      password: [
        { required: true, message: '', trigger: 'change' },
        { min: 6, message: ' 6 ', trigger: 'change' },
        { max: 12, message: ' 12 ', trigger: 'change' },
      ],
      repeatPassword: [
        { validator: validatorFunction, trigger: 'change', required: true },
      ],
      hobby: [
        { required: true, message: '', trigger: 'change' },
      ],
    };

    const attributes = [
      {
        attribute: 'model',
        description: '',
        type: 'object',
        option: '',
        default: '',
      },
      {
        attribute: 'rules',
        description: '',
        type: 'object',
        option: '',
        default: '',
      },
      {
        attribute: 'showMessage',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: 'true',
      },
      {
        attribute: 'labelWidth',
        description: 'label ',
        type: 'string',
        option: '',
        default: 'auto',
      },
    ];

    const events = [
      {
        name: 'validate',
        description: ' Promise .then ',
        type: '(callback: (val: boolean) => void) => Promise<boolean>',
      },
      {
        name: 'reset',
        description: '',
        type: '() => void',
      },
    ];

    const itemAttributes = [
      {
        attribute: 'label',
        description: '',
        type: 'string',
        option: '',
        default: '',
      },
      {
        attribute: 'prop',
        description: ' model  validatereset ',
        type: 'string',
        option: ' Form  model ',
        default: '',
      },
      {
        attribute: 'showMessage',
        description: '',
        type: 'boolean',
        option: 'true | false',
        default: '',
      },
      {
        attribute: 'required',
        description: ' prop ',
        type: 'boolean',
        option: 'true | false',
        default: '',
      },
      {
        attribute: 'requiredErrorMsg',
        description: ' required  msg',
        type: 'string',
        option: '',
        default: '{prop} field is required',
      },
      {
        attribute: 'requiredTrigger',
        description: ' required ',
        type: 'string',
        option: 'change | blur',
        default: 'blur',
      },
    ];

    return {
      username1,
      username2,
      username3,
      rules,
      formRef,
      attributes,
      events,
      itemAttributes,
      handleCreate,
      handleReset,
    };
  },
});
</script>

<style lang='less' scoped>
.tip {
  color: var(--color);
  font-size: 14px;
  text-indent: 2rem;
  line-height: 1.6;

  a {
    color: var(--primary-shallow-color);
    cursor: pointer;
  }
}
</style>
